import React from "react";
import Menu from "./Menu.jsx";
import MenuContainer from "./MenuContainer";
import wall from "./pictures/backgrounds/wall2.jpg";
import location from './pictures/location.svg';
import phone from './pictures/phone.svg';
import envelope from './pictures/envelope.svg';
import Paper from '@material-ui/core/Paper';

class Drinks extends React.Component {
  style = {
    backgroundImage: `url(${wall})`,
    display: "flex",
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh'
  };

  handleMap = () => {
        if /* if we're on iOS, open in Apple Maps */
          ((navigator.platform.indexOf("iPhone") != -1) || 
           (navigator.platform.indexOf("iPad") != -1) || 
           (navigator.platform.indexOf("iPod") != -1))
          window.open("https://www.google.com/maps/dir//Kigaru+Sushi,+3486+SW+Cedar+Hills+Blvd,+Beaverton,+OR+97005/@45.485315,-122.7874241,13.98z/data=!4m8!4m7!1m0!1m5!1m1!1s0x54950c6cb3f725c5:0xfd4b05948392c357!2m2!1d-122.8090081!2d45.4945878");
      else /* else use Google */
          window.open("https://www.google.com/maps/dir//Kigaru+Sushi,+3486+SW+Cedar+Hills+Blvd,+Beaverton,+OR+97005/@45.485315,-122.7874241,13.98z/data=!4m8!4m7!1m0!1m5!1m1!1s0x54950c6cb3f725c5:0xfd4b05948392c357!2m2!1d-122.8090081!2d45.4945878");
  }

  render() {
    return (
      <div style={this.style} className="menu-background">
        {!this.props.desktop && (
          <div style={this.style} className="mobile-menu-back" />
        )}
        <div className="contact-form">
          <ul>
            <Paper elevation={3} style={{margin: '20px'}}>
              <div onClick={this.handleMap} style={{cursor: 'pointer'}}>
                <li>
                  <img src={location} />
                  <p>
                      Click to open maps to Kigaru (iOS or Android!)
                  </p>
                </li>
              </div>
            </Paper>
            <Paper elevation={3} style={{margin: '20px'}}>
              <a href="tel:123-123-1234">
                <li>
                  <img src={phone} />
                  <p>
                      Call Us @123-123-1234
                  </p>
                </li>
              </a>
            </Paper>
            <Paper elevation={3} style={{margin: '20px'}}>
              <a href="mailto:test@gmail.com">
                <li>
                  <img src={envelope} />
                  <p>
                      Email Us test@gmail.com
                  </p>
                </li>
              </a>
            </Paper>
          </ul>
        </div>
      </div>
    );
  }
}

export default Drinks;